.navbar
{
    display: flex;
    justify-content: space-around;
    padding: 10px;
    background-color: gainsboro;
    box-shadow: 0 1px 3px -2px black;
}
.nav-logo {
    display: flex;
    align-items: center;
    gap: 10px;
}
.nav-logo p{
    color:black;
    font-size:  38px;
    font-weight: 600;
}
.nav-logo img{
    height: 60px;
}
.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: #626262;
    font-size: 20px;
    font-weight: 500;
}
.nav-menu li{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
    background-color: cornflowerblue;
    padding: 5px;
    width: 100px;
    border: 2px solid cornflowerblue;
    border-radius: 50px;
}
.nav-menu li:active{
    border: none;
}

.nav-menu hr{
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background-color: cornflowerblue;
}
.nav-login-cart{
    display: flex;
    align-items: center;
    gap: 45px;
}

.nav-login-cart button{
    width: 120px;
    height: 45px;
    outline: none;
    border: 2px solid black;
    border-radius: 50px;
    color: white;
    font-size: 20px;
    font-weight: 500;
    background: black;
    cursor: pointer;
}
.nav-login-cart button:active{
    border: 2px solid gainsboro;
}
.nav-cart-count{
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -35px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 14px;
    background: cornflowerblue;
    color: white;
}